<template>
  <div>
    <v-row>
      <v-col md="4"> </v-col>
      <v-col md="4">
        <v-col cols="">
          <v-text-field
            label="book"
            single-line
            outlined
            type="text"
            class="todo-input"
            v-model="newTodo"
            @keyup.enter="addTodo"
          ></v-text-field>
        </v-col>
      </v-col>
      <v-col md="4"> 
        <v-btn @click="addTodo">ค้นหา</v-btn>
      </v-col>
    </v-row>

    <v-divider class="mt-2 mb-2"></v-divider>
<div d-flex justify-center mb-6> 
      <v-card
        v-for="manga in results"
        :key="manga.id"
        class="ma-2"
        max-width="344"
        outlined
        @click="handleManagaclick(manga)"
      >
        <v-list-item three-line>
          <v-list-item-content>
            <div class="overline mb-4">Publishing:{{ manga.Field }}</div>
            <v-list-item-title class="headline mb-1">
              {{ manga.title }}
            </v-list-item-title>
            <v-list-item-subtitle>{{ manga.Subfield }}</v-list-item-subtitle>
          </v-list-item-content>

          <img
            :src="manga.image_url"
            alt=""
            :style="{ width: '80px', marginTop: '30px' }"
          />
        </v-list-item>
      </v-card>
      <!-- <div>{{$store.getters.getBookname}}</div>  -->
      <!-- <div><input v-model="form.name"></div> -->
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "todo-input",
  data() {
    return {
      newTodo: "",
      results: [],
    };
  },

  mounted() {
    //console.log(this.$route.query.textSearch);
    this.newTodo = this.$route.query.textSearch;
  },

  methods: {
    addTodo() {
      // if (this.newTodo.trim() == 0) {
      //   return;
      // }
      //this.newTodo = "";
      //const url = `https://api.jikan.moe/v3/search/manga?q=${this.newTodo}&page=1`; //https://api.jikan.moe/v3/search/manga?q=soma&page=1
      const url = `https://autolibrary-cisproject.herokuapp.com/bibdata/findbook/${this.newTodo}?StartPage=1&perPage=5`;
      axios.get(url).then((res) => {
        //console.log(res);
        this.results = JSON.stringify(res.data.Results);
        console.log(this.results);
      });
    },
    handleManagaclick(manga) {
      window.location = manga.url;
    },
  },
};
</script>

<style lang="css" scoped></style>
